.app--header {
    margin: 0;
    text-align: center;
    font-weight: 500;
    width: 100%;
    height: 10%;
    padding: 20px 0px;
    color: white;
    background-color: rgb(97, 132, 174);
    position: relative;
}

.app--header nav {
    background-color: white;
    justify-content: space-evenly;
}


.sorted--sec {
    display: flex;
    width: auto;
    justify-content: space-between;
    font-size: 14px;
    padding: 0px 10px;
}

.sorted-indic {
    color: black;
    margin-left: 20px;
}

.clear-sel {
    color: rgb(122, 133, 148);
    font-weight: 500;
    transition: font-weight 0.03s;
    float: right;
}

.clear-sel:hover {
    font-weight: 600;
    cursor: pointer;
}

.sort-interact {
    display: flex;
    align-items: center;
}

.sel-op {
    font-size: 16px;
    font-weight: 500;
    color: rgb(122, 133, 148);
    padding: 0px 5px;
}

.sel-op:hover {
    cursor: pointer;
    color: rgb(106, 115, 128);
}

#layout {
    font-size: 14px;
}