.tile--container {
    display: flex;
    justify-content: center;
    width: auto;
    height: auto;
    padding: 50px 20px;
}

.tile--about {
    margin-left: 150px;
    width: 40%;
    height: 20%;
    text-align: left;
}

.book-cover {
    width: 10rem;
    max-height: 15rem;
    aspect-ratio: 2/3;
    border-radius: 0.2rem;
    box-shadow: 0.15rem 0.15rem 0.15rem 0.15rem lightgray;
    display: inline-block;
}

.author {
    margin-top: 0;
    margin-bottom: 0;
    font-weight: 350;
}

.title {
    margin-top: 0;
}

.tags > span {
    transition: font-weight 0.07s;
    border-radius: 0.2rem;
}

.tags > span:hover {
    background-color: rgb(245, 245, 245);
    font-weight: 600;
    cursor: pointer;
}

.favorite-indicator {
    width: 1.5rem;
    aspect-ratio: 1/1;
    padding: 0;
}

.description {
    width: 100%;
    height: 100%;
    overflow: hidden;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;
    display: -webkit-box;;
}