.section--container {
    margin: 20px;
    display: flex;
}

.cover {
    width: 8rem;
    aspect-ratio: 2/3;
    border-radius: 0.2rem;
    box-shadow: 0.15rem 0.15rem 0.15rem 0.15rem lightgray;
    margin: 0.5rem;
    opacity: 40%;
} 

.cover-selected {
    width: 8rem;
    aspect-ratio: 2/3;
    border-radius: 0.2rem;
    box-shadow: 0.15rem 0.15rem 0.15rem 0.15rem lightgray;
    margin: 0.5rem;
} 

.book-covers {
    width: 60%;
}

.book-covers > img:hover {
    cursor: pointer;
}

.book-profile {
    width: 40%;
}